export default {
    async asyncData({$content}){
        const post = await $content("posts/hello").fetch();
        return {post}
    },
    data(){
        return {
            title:"this is title",
            loggedIn: this.$auth.loggedIn
        }
    },
    methods:{
        async logout(){
            await this.$auth.logout();
            this.$router.push('/login');
        }
    }
}
